:root {
  --color-primary: #c69963;
  --color-primary-dark: #B28451;
  --color-secondary: #101d2c;
  --color-grey-light-1: #f9f7f6;
  --color-grey-light-2: #aaa;
  --color-grey-dark-1: #54483A;
  --color-grey-dark-2: #6D5D4B;
  --font-primary: Nunito, sans-serif;
  --font-display: Josefin Sans, sans-serif;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  font-family: var(--font-primary);
}
@media only screen and (max-width: 75em) {
  html {
    font-size: 50%;
  }
}

.container {
  display: grid;
  grid-template-rows: 80vh min-content 40vw repeat(3, min-content);
  grid-template-columns: [sidebar-start] 8rem [sidebar-end full-start] minmax(6rem, 1fr) [center-start] repeat(8, [col-start] minmax(min-content, 14rem) [col-end]) [center-end] minmax(6rem, 1fr) [full-end];
}
@media only screen and (max-width: 62.5em) {
  .container {
    display: grid;
    grid-template-rows: 6rem 80vh min-content 40vw repeat(3, min-content);
    grid-template-columns: [full-start] minmax(6rem, 1fr) [center-start] repeat(8, [col-start] minmax(min-content, 14rem) [col-end]) [center-end] minmax(6rem, 1fr) [full-end];
  }
}
@media only screen and (max-width: 50em) {
  .container {
    grid-template-rows: 6rem calc(100vh - 6rem);
  }
}

.h4, .h3, .h2, .h1 {
  font-family: var(--font-display);
  font-weight: 400;
}

.h1 {
  font-size: 4.5rem;
  color: var(--color-grey-light-1);
  line-height: 1;
}

.h2 {
  font-size: 4rem;
  line-height: 1;
  font-style: italic;
}
.h2--light {
  color: var(--color-grey-light-1);
}
.h2--dark {
  color: var(--color-grey-dark-1);
}

.h3 {
  font-size: 2rem;
  color: var(--color-primary);
  text-transform: uppercase;
}

.h4 {
  font-size: 1.9rem;
}
.h4--light {
  color: var(--color-grey-light-1);
}
.h4--dark {
  color: var(--color-grey-dark-1);
}

.btn {
  background-color: var(--color-primary);
  color: white;
  border: none;
  border-radius: 0;
  font-family: var(--font-display);
  font-size: 1.5rem;
  text-transform: uppercase;
  padding: 1.8rem 3rem;
  cursor: pointer;
  transition: all 0.2s;
}
.btn:hover {
  background-color: var(--color-primary-dark);
}

.me-sm {
  margin-bottom: 2rem;
}

.me-md {
  margin-bottom: 3rem;
}

.me-lg {
  margin-bottom: 4rem;
}

.me-xl {
  margin-bottom: 8rem;
}

.features {
  grid-column: center-start/center-end;
  margin: 15rem 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  gap: 6rem;
  align-items: start;
}

.feature {
  display: grid;
  grid-template-columns: min-content 1fr;
  row-gap: 1rem;
  column-gap: 2rem;
}
.feature__icon {
  width: 4.5rem;
  height: 4.5rem;
  fill: var(--color-primary);
  grid-column: 1/1;
  grid-row: 1/1;
}
.feature .h4 {
  align-self: center;
}
.feature__text {
  font-size: 1.7rem;
  grid-column: 2/-1;
}

.footer {
  background-color: var(--color-secondary);
  grid-column: full-start/full-end;
  padding: 8rem;
}
.footer .nav {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
  gap: 2rem;
  list-style: none;
  text-align: center;
  align-items: center;
}
.footer .nav__link:link, .footer .nav__link:visited {
  font-size: 1.4rem;
  font-family: var(--font-display);
  text-transform: capitalize;
  text-decoration: none;
  color: white;
  text-align: center;
  padding: 1.5rem;
  display: inline-block;
  transition: all 0.2s;
}
.footer .nav__link:hover, .footer .nav__link:active {
  background-color: rgba(255, 255, 255, 0.5);
  transform: translateY(-3px);
}
.footer .copyright {
  color: var(--color-grey-light-2);
  font-size: 1.4rem;
  text-align: center;
  margin-top: 6rem;
  margin-left: auto;
  margin-right: auto;
  width: 70%;
}

.header {
  background-color: purple;
  grid-column: full-start/col-end 6;
  background-image: linear-gradient(rgba(16, 29, 44, 0.93), rgba(16, 29, 44, 0.93)), url("../../img/hero.jpeg");
  background-position: center;
  padding: 8rem;
  padding-top: 4rem;
  display: grid;
  grid-template-rows: 1fr min-content minmax(6rem, min-content) 1fr;
  grid-template-columns: minmax(min-content, max-content);
  row-gap: 1.5rem;
  justify-content: center;
}
@media only screen and (max-width: 50em) {
  .header {
    grid-column: 1/-1;
  }
}
@media only screen and (max-width: 37.5em) {
  .header {
    padding: 5rem;
  }
}
.header__logo {
  height: 3rem;
  justify-self: center;
}
.header__btn {
  align-self: start;
  justify-self: start;
}
.header__seenon-text {
  display: grid;
  grid-template-columns: 1fr max-content 1fr;
  font-size: 1.6rem;
  color: var(--color-grey-light-2);
  column-gap: 1.5rem;
  align-items: center;
}
.header__seenon-text::after, .header__seenon-text::before {
  content: "";
  height: 1px;
  display: block;
  background-color: currentColor;
}
.header__seenon-logos {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 3rem;
  justify-items: center;
  align-items: center;
}
.header__seenon-logos img {
  max-height: 2.5rem;
  max-width: 100%;
  filter: brightness(70%);
}

.realtors {
  background-color: var(--color-secondary);
  grid-column: col-start 7/full-end;
  padding: 3rem;
  display: grid;
  align-content: center;
  justify-content: center;
  justify-items: center;
  row-gap: 2rem;
}
@media only screen and (max-width: 50em) {
  .realtors {
    grid-column: 1/-1;
  }
}
.realtors__list {
  display: grid;
  grid-template-columns: min-content max-content;
  column-gap: 2rem;
  row-gap: 5vh;
  align-items: center;
}
@media only screen and (max-width: 50em) {
  .realtors__list {
    grid-template-columns: repeat(3, min-content max-content);
  }
}
@media only screen and (max-width: 37.5em) {
  .realtors__list {
    grid-template-columns: min-content max-content;
  }
}
.realtors__img {
  width: 7rem;
  border-radius: 50%;
  display: block;
}
.realtors__sold {
  text-transform: uppercase;
  color: var(--color-grey-light-2);
  margin-top: -3px;
}

.homes {
  grid-column: center-start/center-end;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  gap: 7rem;
  padding: 3rem;
}

.home {
  background-color: var(--color-grey-light-1);
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.home__img {
  width: 100%;
  grid-column: 1/-1;
  grid-row: 1/2;
  z-index: 1;
}
.home__like {
  width: 2.5rem;
  height: 2.5rem;
  fill: var(--color-primary);
  z-index: 2;
  grid-column: 2/3;
  grid-row: 1/2;
  margin: 1rem;
  justify-self: right;
}
.home__name {
  grid-column: 1/-1;
  background-color: var(--color-secondary);
  width: 80%;
  justify-self: center;
  transform: translateY(-50%);
  z-index: 3;
  font-family: var(--font-display);
  color: white;
  font-weight: 400;
  padding: 1.5rem;
  text-align: center;
  font-size: 1.6rem;
}
.home__location, .home__area, .home__rooms, .home__price {
  padding: 0 3rem;
  display: flex;
  align-items: center;
  font-size: 1.5rem;
}
.home__location svg, .home__area svg, .home__rooms svg, .home__price svg {
  width: 2rem;
  height: 2rem;
  fill: var(--color-primary);
}
.home__location p, .home__area p, .home__rooms p, .home__price p {
  margin-left: 1rem;
}
.home__area, .home__price {
  padding: 2rem 3rem;
}
.home__btn {
  grid-column: 1/-1;
}

.gallery {
  background-color: var(--color-grey-light-1);
  grid-column: full-start/full-end;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat 7, 5vw;
  gap: 1.5rem;
  padding: 1.5rem;
}
.gallery__item--1 {
  grid-row: 1/3;
  grid-column: 1/3;
}
.gallery__item--2 {
  grid-row: 1/4;
  grid-column: 3/6;
}
.gallery__item--3 {
  grid-row: 1/3;
  grid-column: 6/7;
}
.gallery__item--4 {
  grid-row: 1/3;
  grid-column: 7/-1;
}
.gallery__item--5 {
  grid-row: 3/6;
  grid-column: 1/3;
}
.gallery__item--6 {
  grid-row: 4/6;
  grid-column: 3/5;
}
.gallery__item--7 {
  grid-row: 4/5;
  grid-column: 5/6;
}
.gallery__item--8 {
  grid-row: 3/5;
  grid-column: 6/8;
}
.gallery__item--9 {
  grid-row: 3/6;
  grid-column: 8/-1;
}
.gallery__item--10 {
  grid-row: 6/8;
  grid-column: 1/2;
}
.gallery__item--11 {
  grid-row: 6/8;
  grid-column: 2/4;
}
.gallery__item--12 {
  grid-row: 6/8;
  grid-column: 4/5;
}
.gallery__item--13 {
  grid-row: 5/8;
  grid-column: 5/8;
}
.gallery__item--14 {
  grid-row: 6/8;
  grid-column: 8/-1;
}
.gallery__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.sidebar {
  background-color: var(--color-primary);
  grid-column: sidebar-start/sidebar-end;
  grid-row: 1/-1;
  padding: 2rem;
  display: flex;
  justify-content: center;
}
@media only screen and (max-width: 62.5em) {
  .sidebar {
    grid-row: 1/2;
    grid-column: 1/-1;
    justify-content: end;
    align-items: center;
  }
}

.nav-btn {
  border: none;
  border-radius: 0;
  width: 4.5rem;
  height: 2px;
  border-color: white;
  margin-top: 1.2rem;
}
@media only screen and (max-width: 62.5em) {
  .nav-btn {
    margin-right: 1rem;
    margin-top: 0;
  }
}
.nav-btn::before, .nav-btn::after {
  content: "";
  display: block;
  width: 4.5rem;
  height: 2px;
  background-color: inherit;
}
.nav-btn::before {
  transform: translateY(-1.2rem);
}
.nav-btn::after {
  transform: translateY(1rem);
}

.story__pictures {
  grid-column: full-start/col-end 4;
  background-image: linear-gradient(rgba(198, 153, 99, 0.5), rgba(198, 153, 99, 0.5)), url("../../img/back.jpg");
  background-size: cover;
  display: grid;
  grid-template-rows: repeat(6, 1fr);
  grid-template-columns: repeat(6, 1fr);
  align-items: center;
}
@media only screen and (max-width: 50em) {
  .story__pictures {
    grid-column: 1/-1;
    padding: 3rem;
  }
}
.story__img--1 {
  width: 100%;
  grid-column: 2/6;
  grid-row: 2/6;
  box-shadow: 0 2rem 5rem rgba(0, 0, 0, 0.2);
}
@media only screen and (max-width: 50em) {
  .story__img--1 {
    grid-column: 1/5;
    grid-row: 1/-1;
    height: 100%;
  }
}
.story__img--2 {
  width: 115%;
  grid-column: 4/7;
  grid-row: 4/6;
  z-index: 20;
  box-shadow: 0 2rem 5rem rgba(0, 0, 0, 0.2);
}
@media only screen and (max-width: 50em) {
  .story__img--2 {
    width: 100%;
    grid-row: 1/-1;
  }
}
.story__content {
  background-color: var(--color-grey-light-1);
  grid-column: col-start 5/full-end;
  padding: 6rem 8vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
@media only screen and (max-width: 50em) {
  .story__content {
    grid-column: 1/-1;
    grid-row: 5/6;
  }
}
.story__text {
  font-size: 1.5rem;
  font-style: italic;
}
